/* stylelint-disable */

@mixin bigdesktop {
  @media (max-width: (1500px)) {
    @content;
  }
}

@mixin desktop {
  @media (max-width: (1200px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (992px)) {
    @content;
  }
}

@mixin smalltablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}

@mixin minimobile {
  @media (max-width: (375px)) {
    @content;
  }
}
